body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color {

  background: #cc2b5e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left top, right top, from(#753a88), to(#cc2b5e));
background: linear-gradient(to right, #753a88, #cc2b5e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.landing-grid {
  background: #cc2b5e;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, left top, right top, from(#753a88), to(#cc2b5e));
background: linear-gradient(to right, #753a88, #cc2b5e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

text-align: center;;
position: fixed;;
top:0;
left:0;
width:100%;
height:100%;

}
.avatar-img{
  width: 300px;
height: 250px;
padding-top: 5em;

}
.banner-text{
background-color: black;
opacity: .8;
width: 75%;
margin: auto;
border-radius: 10px;


}

.banner-text h1{

font-size: 66px;
font-weight: bold;
color: white;


}
.banner-text hr{
border-top: 5px dotted white;
width: 50%;
margin: auto;
}
.banner-text p{
color: white;
font-size: 25px;
padding: 1em;


}
.social-links {
display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;



}
.contact-body{
margin: auto;
background: grey;
position: fixed;
width: 100%;
height: 100%;
justify-content: center;
padding-top: 2em;


}
.contact-grid {
text-align: center;
justify-content: center;
width:80%;
height: 550px;
background: white;
color : black;
border: 2px solid red;
margin-bottom: 20px;



}
.contact-grid h2 {
font-family: 'Anton', sans-serif;



}
.contact-grid p {
font-family: 'Oxygen', sans-serif;
font-weight: bold;



}
.contact-grid hr {
border-top: 5px dotted black;
width: 50%;
margin: auto;




}
.contact-list i{
font-size: 66px;
padding-right: 5rem;


}
.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;



}
.project-grid{
display: flex;


}
